body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  width: 100%;
}

h1 {
  color: #ffffff;
  margin: 10px;
}

hr {
  width: 30%;
  height: 2px;
  border: none;
  border-radius: 20px;
  color: white;
  background-color: #ffffff;
  margin: 5px auto 20px auto;
}

.google-map {
  height: 40vh;
  width: 80%;
  margin: 10px;
}
.google-map div {
  border-radius: 15px;
}

.hhicon {
  width: 35px;
  height: 5px;
  background-color: #fff;
  margin: 6px 0;
  border-radius: 5px;
  box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
}

.hh:hover {
  cursor: pointer;
}

.hh {
  padding: 5px 20px;
  position: fixed;
  z-index: 2;
  
}

.sidebar a {
  text-align: center;
  color: #3498db;
  margin: 10px 30px;
  display: block;
  font-size: 2em;
  background-color: #2c3e50;
  padding: 5px 10px;
  border-radius: 5px;
}

.sidebar a:hover {
  cursor: pointer;
  color: #2980b9;
}

#mdiv {
  width:30px;
  height:30px;
  margin: 20px;
}
#mdiv:hover {
  cursor: pointer;
}
.mdiv {
  height:30px;
  width:2px;
  margin-left:12px;
  background-color:black;
  transform: rotate(45deg);
  -ms-transform: rotate(45deg); /* IE 9 */
  -webkit-transform: rotate(45deg); /* Safari and Chrome */
  Z-index:1;
}
.md {
  height:30px;
  width:2px;
  background-color:black;
  transform: rotate(90deg);
  -ms-transform: rotate(90deg); /* IE 9 */
  -webkit-transform: rotate(90deg); /* Safari and Chrome */
  Z-index:2;
}
.videoSection {
    top: 0;
    min-height: 100vh;
    width: 100%;
    background-color: #2c3e50;
    display: flex;
    flex-direction: column;
    font-size: calc(10px + 2vmin);
    color: white;
}

.video {
    margin: 15px;
}
.App-header {
    background-color: #282c34;
    background-image: url('https://upload.wikimedia.org/wikipedia/commons/2/2c/Rotating_earth_%28large%29.gif');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    width: 100%;
  }
footer {
    max-height: 100vh;
    min-height: 10vh;
    background-color: #7f8c8d;
    overflow: hidden;
}

.socialButton {
    margin: 12px;
}
.tagebuch {
    height: 100vh;
    background-color: #2980b9;
    display: block;
    color: white;
}

textarea {
    width: 70%;
    min-height: 50vh;
    border: none;
    border-radius: 5px;
    resize: none;
    font-size: 1.5em;
    box-shadow: 2px 10px 5px 0px rgba(0,0,0,0.75);
    padding: 12px;
    background-color: #ffffff;
}
.gallery {
    display: block;
    height: auto;
    width: 100%;
    overflow: hidden;
}
